.feedbackInfo {
    display: flex;
    align-items: center;
    padding-top: 18px;
    padding-bottom: 50px;
  }
  
  .feedbackName > a {
    color: var(--green-color-highlight-shamrock);
  }
  
  .feedback {
  font-style: italic;
  }
  
  /* .feedbackAvatar {
    width: 50px;
    height: 50px;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 20px;
  } */
  
  .loadingIndicator {
    color: var(--green-color-highlight-shamrock);
    width: 20px;
    height: 20px;
    animation:spin 2s linear infinite;
  }
  
  @keyframes spin { 
    100% { 
        -webkit-transform: rotate(360deg); 
        transform:rotate(360deg); 
    } 
  }
  
  .errorMessage {
    text-align: center;
    color: var(--red-error-color);
    font-weight: 400;
  }